<template>
    <input type="file" @change="insImgToBg"  accept="image/*" >
    <br>
    左上
    {{nodeData_L_T}}<br>
    左下
    {{nodeData_L_B}}<br>
    右上
    {{nodeData_R_T}}<br>
    右下
    {{nodeData_R_B}}<br>
    <br>
    左上P
    {{nodeData_L_T_P}}<br>
    左下P
    {{nodeData_L_B_P}}<br>
    右上P
    {{nodeData_R_T_P}}<br>
    右下P
    {{nodeData_R_B_P}}<br>
    <!-- <div
      id="box"
      class="mainBox"
      @mousemove="move"
      @mouseup="moveEnd"
      :style="{
        width: canvas_width + 'px',
        height: canvas_height + 'px',
        'background-image': 'url(' + img_file + ')'
      }"
    > -->
    <div
      id="box"
      class="mainBox"
      @mousemove="move"
      @mouseup="this.status = null;"
      :style="{
        width: canvas_width + 'px',
        height: canvas_height + 'px',
        'background-image': 'url(' + img_file + ')'
      }"
    >
      <div
        id="node_L_T"
        @mousedown="moveStart_L_T"
        :style="{
          left: nodeData_L_T.x + 'px',
          top: nodeData_L_T.y + 'px',
        }"
        class="node"
      ></div>

      <div
        id="node_L_B"
        @mousedown="moveStart_L_B"
        :style="{
          left: nodeData_L_B.x + 'px',
          top: nodeData_L_B.y + 'px',
        }"
        class="node"
      ></div>

      <div
        id="node_R_T"
        @mousedown="moveStart_R_T"
        :style="{
          left: nodeData_R_T.x + 'px',
          top: nodeData_R_T.y + 'px',
        }"
        class="node"
      ></div>

      <div
        id="node_R_B"
        @mousedown="moveStart_R_B"
        :style="{
          left: nodeData_R_B.x + 'px',
          top: nodeData_R_B.y + 'px',
        }"
        class="node"
      ></div>
    </div>
    <br>
    <a-button @click="moveEnd">透視裁切</a-button>
    <a-button @click="doSharp">銳利化</a-button>
    <br>
    結果
    <br><br>
    <canvas width='4080' height="3080" id="result_canvas" ref="result_canvas" ></canvas>
    <br><br>
    畫布
    <canvas width='4080' height="3080" id="in_canvas" ref="in_canvas"></canvas>
  </template>
  <script>
  var in_canvas = null;
  var result_canvas = null;
  var ctx = null;
  var result_ctx = null;
  //[0A,1B,2C,3D,4E,5F,6G,7H]
  var get_value = [null,null,null,null,null,null,null,null]
  export default {
    data() {
      return {
        canvas_width:500,
        canvas_height:500,
        canvas_data:null,
        img_file:null,
        status: null,
        nodeData_L_T: {
          x: 10,
          y: 10,
        },
        nodeData_L_B: {
          x: 10,
          y: 300,
        },
        nodeData_R_T: {
          x: 300,
          y: 10,
        },
        nodeData_R_B: {
          x: 300,
          y: 300,
        },

        nodeData_L_T_P: {
          x: 10,
          y: 10,
        },
        nodeData_L_B_P: {
          x: 10,
          y: 300,
        },
        nodeData_R_T_P: {
          x: 300,
          y: 10,
        },
        nodeData_R_B_P: {
          x: 300,
          y: 300,
        },
        select_box :"",
        // 移動的起點——鼠標按下時鼠標的坐標
        startX_L_T: null,
        startY_L_T: null,
        startX_L_B: null,
        startY_L_B: null,
        startX_R_T: null,
        startY_R_T: null,
        startX_R_B: null,
        startY_R_B: null,
        nodeX: null,
        nodeY: null,
        boxWidth: 600,
        boxHeight: 500,
        boxX: null,
        boxY: null,
        img:null,
      };
    },
    mounted() {
      let boxDOM = document.getElementById("box");
      this.boxX = boxDOM.clientLeft;
      this.boxY = boxDOM.clientTop;
    },
    methods: {
      insImgToBg(e){
        this.img_file = URL.createObjectURL(e.target.files[0])
        //放入canvas
        in_canvas = document.querySelector("#in_canvas");
        result_canvas = document.querySelector("#result_canvas");
        ctx = in_canvas.getContext("2d");
        result_ctx = result_canvas.getContext("2d");
        
        var img = new Image();
        img.src = this.img_file;
        let this_t = this;
        img.onload = function() {
          this_t.canvas_width= img.width
          this_t.canvas_height= img.height
          ctx.clearRect(0, 0, 4080, 3080);
          ctx.drawImage(img,0,0);
        }
      },
      moveStart_L_T(e) {
        if (e.target.id) {
          this.select_box = "node_L_T";
          this.status = "move";
          this.startX_L_T = e.clientX;
          this.startY_L_T = e.clientY;
  
          let nodeDOM = document.getElementById("node_L_T");
          this.nodeX_L_T = nodeDOM.offsetLeft;
          this.nodeY_L_T = nodeDOM.offsetTop;
        }
      },
      moveStart_L_B(e) {
        if (e.target.id) {
          this.select_box = "node_L_B";
          this.status = "move";
          this.startX_L_B = e.clientX;
          this.startY_L_B = e.clientY;
  
          let nodeDOM = document.getElementById("node_L_B");
          this.nodeX_L_B = nodeDOM.offsetLeft;
          this.nodeY_L_B = nodeDOM.offsetTop;
        }
      },
      moveStart_R_T(e) {
        if (e.target.id) {
          this.select_box = "node_R_T";
          this.status = "move";
          this.startX_R_T = e.clientX;
          this.startY_R_T = e.clientY;
  
          let nodeDOM = document.getElementById("node_R_T");
          this.nodeX_R_T = nodeDOM.offsetLeft;
          this.nodeY_R_T = nodeDOM.offsetTop;
        }
      },
      moveStart_R_B(e) {
        if (e.target.id) {
          this.select_box = "node_R_B";
          this.status = "move";
          this.startX_R_B = e.clientX;
          this.startY_R_B = e.clientY;
  
          let nodeDOM = document.getElementById("node_R_B");
          this.nodeX_R_B = nodeDOM.offsetLeft;
          this.nodeY_R_B = nodeDOM.offsetTop;
        }
      },
      // 移動中
      move(e) {
        if (this.status === "move") {
          if(this.select_box == "node_L_T"){
            this.nodeData_L_T.x = this.nodeX_L_T + (e.clientX - this.startX_L_T);
            this.nodeData_L_T.y = this.nodeY_L_T + (e.clientY - this.startY_L_T);
            // 不能從左側移出盒子
            if (this.nodeData_L_T.x < this.boxX) {
              this.nodeData_L_T.x = this.boxX;
            }
            // 不能從頂部移出盒子
            if (this.nodeData_L_T.y < this.boxY) {
              this.nodeData_L_T.y = this.boxY;
            }
          }else if(this.select_box == "node_L_B"){
            this.nodeData_L_B.x = this.nodeX_L_B + (e.clientX - this.startX_L_B);
            this.nodeData_L_B.y = this.nodeY_L_B + (e.clientY - this.startY_L_B);
            // 不能從左側移出盒子
            if (this.nodeData_L_B.x < this.boxX) {
              this.nodeData_L_B.x = this.boxX;
            }
            // 不能從頂部移出盒子
            if (this.nodeData_L_B.y < this.boxY) {
              this.nodeData_L_B.y = this.boxY;
            }
          }else if(this.select_box == "node_R_T"){
            this.nodeData_R_T.x = this.nodeX_R_T + (e.clientX - this.startX_R_T);
            this.nodeData_R_T.y = this.nodeY_R_T + (e.clientY - this.startY_R_T);
            // 不能從左側移出盒子
            if (this.nodeData_R_T.x < this.boxX) {
              this.nodeData_R_T.x = this.boxX;
            }
            // 不能從頂部移出盒子
            if (this.nodeData_R_T.y < this.boxY) {
              this.nodeData_R_T.y = this.boxY;
            }
          }else if(this.select_box == "node_R_B"){
            this.nodeData_R_B.x = this.nodeX_R_B + (e.clientX - this.startX_R_B);
            this.nodeData_R_B.y = this.nodeY_R_B + (e.clientY - this.startY_R_B);
            // 不能從左側移出盒子
            if (this.nodeData_R_B.x < this.boxX) {
              this.nodeData_R_B.x = this.boxX;
            }
            // 不能從頂部移出盒子
            if (this.nodeData_R_B.y < this.boxY) {
              this.nodeData_R_B.y = this.boxY;
            }
          }
          
  
          // 盒子大小隨拖拽元素的位置自適應變化
          // this.boxWidth = this.nodeData.x + 200;
          // this.boxHeight = this.nodeData.y + 200;
        }
      },

      //銳利化
      doSharp(){
        //先放入圖片
        //讀取
        let rgb_data = ctx.getImageData(0, 0, this.canvas_width, this.canvas_height);
        let dx_one_data = ctx.getImageData(0, 0, this.canvas_width, this.canvas_height);
        let dx_two_data = ctx.getImageData(0, 0, this.canvas_width, this.canvas_height);

        //一階
        //let dx_one_data = rgb_data;
        //二階
        //let dx_two_data = rgb_data;
        //result_ctx.putImageData(rgb_data,0,0);
        
        console.log(this.canvas_width, this.canvas_height)
        let x_p_index = 1;
        let y_p_index = 1;
        let in_rgb_data = null;
        for (let i = 0; i < (rgb_data.data.length/4); i++) {
          if(x_p_index > this.canvas_width){
            y_p_index ++;
            x_p_index = 1;
          }
          //要拿哪個點起始 
          if(y_p_index == 1 || x_p_index==1 || x_p_index == this.canvas_width){
            in_rgb_data = ctx.getImageData(x_p_index, y_p_index, 1, 1).data;
            
            dx_one_data.data[4 * i] = in_rgb_data[0]
            dx_one_data.data[(4 * i)+1] = in_rgb_data[1]
            dx_one_data.data[(4 * i)+2] = in_rgb_data[2]
            dx_two_data.data[4 * i] = in_rgb_data[0]
            dx_two_data.data[(4 * i)+1] = in_rgb_data[1]
            dx_two_data.data[(4 * i)+2] = in_rgb_data[2]
          }else{
            in_rgb_data = ctx.getImageData(x_p_index-1, y_p_index-1, 3,3).data;
            dx_one_data.data[4 * i] = ((in_rgb_data[16]*4)-in_rgb_data[4]-in_rgb_data[12]-in_rgb_data[20]-in_rgb_data[28])
            dx_one_data.data[(4 * i)+1] = ((in_rgb_data[17]*4)-in_rgb_data[5]-in_rgb_data[13]-in_rgb_data[21]-in_rgb_data[29])
            dx_one_data.data[(4 * i)+2] = ((in_rgb_data[18]*4)-in_rgb_data[6]-in_rgb_data[14]-in_rgb_data[22]-in_rgb_data[30])

            dx_two_data.data[4 * i] = ((in_rgb_data[16]*8)-in_rgb_data[4]-in_rgb_data[12]-in_rgb_data[20]-in_rgb_data[28]-in_rgb_data[0]-in_rgb_data[8]-in_rgb_data[24]-in_rgb_data[32])
            dx_two_data.data[(4 * i)+1] =((in_rgb_data[17]*8)-in_rgb_data[5]-in_rgb_data[13]-in_rgb_data[21]-in_rgb_data[29]-in_rgb_data[1]-in_rgb_data[9]-in_rgb_data[25]-in_rgb_data[33])
            dx_two_data.data[(4 * i)+2] = ((in_rgb_data[18]*8)-in_rgb_data[6]-in_rgb_data[14]-in_rgb_data[22]-in_rgb_data[30]-in_rgb_data[2]-in_rgb_data[10]-in_rgb_data[26]-in_rgb_data[34])
          }
          
            rgb_data.data[4 * i] = rgb_data.data[4 * i]+(dx_two_data.data[4 * i]*(dx_one_data.data[4 * i])/255)
            rgb_data.data[[(4 * i)+1]] = rgb_data.data[[(4 * i)+1]]+(dx_two_data.data[(4 * i)+1]*(dx_one_data.data[(4 * i)+1])/255)
            rgb_data.data[[(4 * i)+2]] = rgb_data.data[[(4 * i)+2]]+(dx_two_data.data[(4 * i)+2]*(dx_one_data.data[(4 * i)+2])/255)
          


          
          x_p_index++;


        }
        result_ctx.clearRect(0, 0, 4080, 3080);;
        result_ctx.putImageData(rgb_data,0,0);
        // rgb_data.onload = function() {
        //   this_t.canvas_width= img.width
        //   this_t.canvas_height= img.height
        //   ctx.drawImage(rgb_data,0,0);
        // }
      },
      // 停止移動
      moveEnd() {
        //處理 X距離
        let x_min_array = [this.nodeData_R_T.x - this.nodeData_L_T.x, this.nodeData_R_B.x - this.nodeData_L_T.x,this.nodeData_R_T.x - this.nodeData_L_B.x, this.nodeData_R_B.x - this.nodeData_L_B.x];
        let X_min = Math.min(...x_min_array)   ;

        //處理 Y距離
        let Y_min_array = [this.nodeData_R_B.y - this.nodeData_L_T.y, this.nodeData_R_B.y - this.nodeData_R_T.y,this.nodeData_L_B.y - this.nodeData_L_T.y, this.nodeData_L_B.y - this.nodeData_R_T.y];
        let Y_min = Math.min(...Y_min_array)   ;

        // 關閉移動狀態
        //this.status = null;

        //新的點
        this.nodeData_L_T_P.x = 1;
        this.nodeData_L_T_P.y = 1;

        this.nodeData_L_B_P.x = 1;
        this.nodeData_L_B_P.y = 1+Y_min;

        this.nodeData_R_T_P.x = 1+X_min;
        this.nodeData_R_T_P.y = 1;

        this.nodeData_R_B_P.x = 1+X_min;
        this.nodeData_R_B_P.y = 1+Y_min;

        result_ctx.width = X_min;
        result_ctx.height = Y_min;
        result_ctx.clearRect(0, 0, 1980, 1080);
        
        let rgb_data = ctx.getImageData(0, 0, X_min, Y_min);
        result_ctx.putImageData(rgb_data,0,0);
        //解方程式 
        //[X,A,B,C,D]
        let X_equation = [
          [this.nodeData_L_T.x , this.nodeData_L_T_P.x , this.nodeData_L_T_P.y , this.nodeData_L_T_P.x*this.nodeData_L_T_P.y , 1],
          [this.nodeData_L_B.x , this.nodeData_L_B_P.x , this.nodeData_L_B_P.y , this.nodeData_L_B_P.x*this.nodeData_L_B_P.y , 1],
          [this.nodeData_R_T.x , this.nodeData_R_T_P.x , this.nodeData_R_T_P.y , this.nodeData_R_T_P.x*this.nodeData_R_T_P.y , 1],
          [this.nodeData_R_B.x , this.nodeData_R_B_P.x , this.nodeData_R_B_P.y , this.nodeData_R_B_P.x*this.nodeData_R_B_P.y , 1]
        ];
        //[Y,E,F,G,H]
        let Y_equation = [
          [this.nodeData_L_T.y , this.nodeData_L_T_P.x , this.nodeData_L_T_P.y , this.nodeData_L_T_P.x*this.nodeData_L_T_P.y , 1],
          [this.nodeData_L_B.y , this.nodeData_L_B_P.x , this.nodeData_L_B_P.y , this.nodeData_L_B_P.x*this.nodeData_L_B_P.y , 1],
          [this.nodeData_R_T.y , this.nodeData_R_T_P.x , this.nodeData_R_T_P.y , this.nodeData_R_T_P.x*this.nodeData_R_T_P.y , 1],
          [this.nodeData_R_B.y , this.nodeData_R_B_P.x , this.nodeData_R_B_P.y , this.nodeData_R_B_P.x*this.nodeData_R_B_P.y , 1]
        ]
        
        
        //先處理掉A  X
        let temp_A = [
          [
            (X_equation[0][0]*X_equation[1][1])-(X_equation[1][0]*X_equation[0][1]),  //X
            (X_equation[0][2]*X_equation[1][1])-(X_equation[1][2]*X_equation[0][1]),  //B
            (X_equation[0][3]*X_equation[1][1])-(X_equation[1][3]*X_equation[0][1]),  //C
            (X_equation[0][4]*X_equation[1][1])-(X_equation[1][4]*X_equation[0][1]),  //D
          ],
          [
            (X_equation[1][0]*X_equation[2][1])-(X_equation[2][0]*X_equation[1][1]),
            (X_equation[1][2]*X_equation[2][1])-(X_equation[2][2]*X_equation[1][1]),
            (X_equation[1][3]*X_equation[2][1])-(X_equation[2][3]*X_equation[1][1]),
            (X_equation[1][4]*X_equation[2][1])-(X_equation[2][4]*X_equation[1][1]),
          ],
          [
            (X_equation[2][0]*X_equation[3][1])-(X_equation[3][0]*X_equation[2][1]),
            (X_equation[2][2]*X_equation[3][1])-(X_equation[3][2]*X_equation[2][1]),
            (X_equation[2][3]*X_equation[3][1])-(X_equation[3][3]*X_equation[2][1]),
            (X_equation[2][4]*X_equation[3][1])-(X_equation[3][4]*X_equation[2][1]),
          ]
        ]
        //檢查陣列 第二個是否為正
        if(temp_A[0][1] < 0){
          temp_A[0][0] *=-1; 
          temp_A[0][1] *=-1; 
          temp_A[0][2] *=-1; 
          temp_A[0][3] *=-1; 
        }
        if(temp_A[1][1] < 0){
          temp_A[1][0] *=-1; 
          temp_A[1][1] *=-1; 
          temp_A[1][2] *=-1; 
          temp_A[1][3] *=-1; 
        }
        if(temp_A[2][1] < 0){
          temp_A[2][0] *=-1; 
          temp_A[2][1] *=-1; 
          temp_A[2][2] *=-1; 
          temp_A[2][3] *=-1; 
        }
        //拿掉B
        let temp_B = [
          [
            (temp_A[0][0]*temp_A[1][1])-(temp_A[1][0]*temp_A[0][1]),  //X
            (temp_A[0][2]*temp_A[1][1])-(temp_A[1][2]*temp_A[0][1]),  //C
            (temp_A[0][3]*temp_A[1][1])-(temp_A[1][3]*temp_A[0][1])  //D
          ],
          [
            (temp_A[1][0]*temp_A[2][1])-(temp_A[2][0]*temp_A[1][1]), 
            (temp_A[1][2]*temp_A[2][1])-(temp_A[2][2]*temp_A[1][1]), 
            (temp_A[1][3]*temp_A[2][1])-(temp_A[2][3]*temp_A[1][1])  
          ]
        ];
        if(temp_B[0][1] < 0){
          temp_B[0][0] *=-1; 
          temp_B[0][1] *=-1; 
          temp_B[0][2] *=-1; 
        }
        if(temp_B[1][1] < 0){
          temp_B[1][0] *=-1; 
          temp_B[1][1] *=-1; 
          temp_B[1][2] *=-1; 
        }
        //拿掉C 取得D [0A,1B,2C,3D,4E,5F,6G,7H]
        get_value[3] = ((temp_B[0][0]*temp_B[1][1])-(temp_B[1][0]*temp_B[0][1]))/((temp_B[0][2]*temp_B[1][1])-(temp_B[1][2]*temp_B[0][1]));
        //放入D 取得C 
        get_value[2] = (temp_B[0][0]-(temp_B[0][2]*get_value[3]))/temp_B[0][1];
        //放入C 取得B
        get_value[1] = (temp_A[0][0]-(temp_A[0][2]*get_value[2])-(temp_A[0][3]*get_value[3]))/temp_A[0][1];
        //放入B 取得A
        get_value[0] = (X_equation[0][0]-(X_equation[0][2]*get_value[1])-(X_equation[0][3]*get_value[2])-(X_equation[0][4]*get_value[3]))/X_equation[0][1];
        
        temp_A = [
          [
            (Y_equation[0][0]*Y_equation[1][1])-(Y_equation[1][0]*Y_equation[0][1]),  //X
            (Y_equation[0][2]*Y_equation[1][1])-(Y_equation[1][2]*Y_equation[0][1]),  //B
            (Y_equation[0][3]*Y_equation[1][1])-(Y_equation[1][3]*Y_equation[0][1]),  //C
            (Y_equation[0][4]*Y_equation[1][1])-(Y_equation[1][4]*Y_equation[0][1]),  //D
          ],
          [
            (Y_equation[1][0]*Y_equation[2][1])-(Y_equation[2][0]*Y_equation[1][1]),
            (Y_equation[1][2]*Y_equation[2][1])-(Y_equation[2][2]*Y_equation[1][1]),
            (Y_equation[1][3]*Y_equation[2][1])-(Y_equation[2][3]*Y_equation[1][1]),
            (Y_equation[1][4]*Y_equation[2][1])-(Y_equation[2][4]*Y_equation[1][1]),
          ],
          [
            (Y_equation[2][0]*Y_equation[3][1])-(Y_equation[3][0]*Y_equation[2][1]),
            (Y_equation[2][2]*Y_equation[3][1])-(Y_equation[3][2]*Y_equation[2][1]),
            (Y_equation[2][3]*Y_equation[3][1])-(Y_equation[3][3]*Y_equation[2][1]),
            (Y_equation[2][4]*Y_equation[3][1])-(Y_equation[3][4]*Y_equation[2][1]),
          ]
        ]
        //檢查陣列 第二個是否為正
        if(temp_A[0][1] < 0){
          temp_A[0][0] *=-1; 
          temp_A[0][1] *=-1; 
          temp_A[0][2] *=-1; 
          temp_A[0][3] *=-1; 
        }
        if(temp_A[1][1] < 0){
          temp_A[1][0] *=-1; 
          temp_A[1][1] *=-1; 
          temp_A[1][2] *=-1; 
          temp_A[1][3] *=-1; 
        }
        if(temp_A[2][1] < 0){
          temp_A[2][0] *=-1; 
          temp_A[2][1] *=-1; 
          temp_A[2][2] *=-1; 
          temp_A[2][3] *=-1; 
        }
        //拿掉B
        temp_B = [
          [
            (temp_A[0][0]*temp_A[1][1])-(temp_A[1][0]*temp_A[0][1]),  //X
            (temp_A[0][2]*temp_A[1][1])-(temp_A[1][2]*temp_A[0][1]),  //C
            (temp_A[0][3]*temp_A[1][1])-(temp_A[1][3]*temp_A[0][1])  //D
          ],
          [
            (temp_A[1][0]*temp_A[2][1])-(temp_A[2][0]*temp_A[1][1]), 
            (temp_A[1][2]*temp_A[2][1])-(temp_A[2][2]*temp_A[1][1]), 
            (temp_A[1][3]*temp_A[2][1])-(temp_A[2][3]*temp_A[1][1])  
          ]
        ];
        if(temp_B[0][1] < 0){
          temp_B[0][0] *=-1; 
          temp_B[0][1] *=-1; 
          temp_B[0][2] *=-1; 
        }
        if(temp_B[1][1] < 0){
          temp_B[1][0] *=-1; 
          temp_B[1][1] *=-1; 
          temp_B[1][2] *=-1; 
        }
        
        //拿掉C 取得D [0A,1B,2C,3D,4E,5F,6G,7H]
        get_value[7] = ((temp_B[0][0]*temp_B[1][1])-(temp_B[1][0]*temp_B[0][1]))/((temp_B[0][2]*temp_B[1][1])-(temp_B[1][2]*temp_B[0][1]));
        //放入D 取得C 
        get_value[6] = (temp_B[0][0]-(temp_B[0][2]*get_value[7]))/temp_B[0][1];
        //放入C 取得B
        get_value[5] = (temp_A[0][0]-(temp_A[0][2]*get_value[6])-(temp_A[0][3]*get_value[7]))/temp_A[0][1];
        //放入B 取得A
        get_value[4] = (Y_equation[0][0]-(Y_equation[0][2]*get_value[5])-(Y_equation[0][3]*get_value[6])-(Y_equation[0][4]*get_value[7]))/Y_equation[0][1];
        
        

        let x_p_index = 1;
        let y_p_index = 1;
        let index_in_x = 1;
        let index_in_y = 1;
        let imageData = result_ctx.getImageData(0, 0, X_min, Y_min);

        for (let i = 0; i < (rgb_data.data.length/4); i++) {
          if(x_p_index > X_min){
            y_p_index ++;
            x_p_index = 1;
          }

          index_in_x = x_p_index*get_value[0]+y_p_index*get_value[1]+x_p_index*y_p_index*get_value[2]+get_value[3];
          index_in_y = x_p_index*get_value[4]+y_p_index*get_value[5]+x_p_index*y_p_index*get_value[6]+get_value[7];

          //要拿哪個點起始
          let temp_X = parseInt(index_in_x)-1;
          let temp_Y = parseInt(index_in_y)-1;

          let in_rgb_data = ctx.getImageData(temp_X, temp_Y, 2, 2).data;

          //算內積 

          //取得權重
          let T_L =  Math.pow(Math.pow(index_in_x-temp_X, 2) + Math.pow(index_in_y-temp_Y, 2),0.5)
          let T_R =  Math.pow(Math.pow((temp_X+1)-index_in_x, 2) + Math.pow(index_in_y-temp_Y, 2),0.5)
          let B_L =  Math.pow(Math.pow(index_in_x-temp_X, 2) + Math.pow((temp_Y+1)-index_in_y, 2),0.5)
          let B_R =  Math.pow(Math.pow((temp_X+1)-index_in_x, 2) + Math.pow((temp_Y+1)-index_in_y, 2),0.5)
          let sum_r =  T_L+T_R+B_L+B_R;
          (in_rgb_data[0]*((sum_r-T_L)/sum_r)+in_rgb_data[4]*((sum_r-T_R)/sum_r)+in_rgb_data[8]*((sum_r-B_L)/sum_r)+in_rgb_data[12]*((sum_r-B_R)/sum_r))/3
          imageData.data[4 * i] = (in_rgb_data[0]*((sum_r-T_L)/sum_r)+in_rgb_data[4]*((sum_r-T_R)/sum_r)+in_rgb_data[8]*((sum_r-B_L)/sum_r)+in_rgb_data[12]*((sum_r-B_R)/sum_r))/3 ; // R 
          imageData.data[4 * i + 1] = (in_rgb_data[1]*((sum_r-T_L)/sum_r)+in_rgb_data[5]*((sum_r-T_R)/sum_r)+in_rgb_data[9]*((sum_r-B_L)/sum_r)+in_rgb_data[13]*((sum_r-B_R)/sum_r))/3; // G
          imageData.data[4 * i + 2] = (in_rgb_data[2]*((sum_r-T_L)/sum_r)+in_rgb_data[6]*((sum_r-T_R)/sum_r)+in_rgb_data[10]*((sum_r-B_L)/sum_r)+in_rgb_data[14]*((sum_r-B_R)/sum_r))/3; // B

          x_p_index ++ ;
        }

        result_ctx.putImageData(imageData, 0, 0);
      },
    },
  };
  </script>
